@keyframes flipY {
  0% {
    transform: translateX(-50%) scale3d(1, 1, 1) rotatex(0deg) rotatey(0deg) rotatez(0deg);
  }
  20.25% {
    transform: translateX(-50%) scale3d(1, 1, 1) rotatex(0deg) rotatey(90deg) rotatez(0deg);
  }
  100% {
    transform: translateX(-50%) scale3d(1, 1, 1) rotatex(0deg) rotatey(90deg) rotatez(0deg);
  }
}
@keyframes flipY-innerCube {
  0% {
    transform: translateX(-50%) scale3d(0.5, 0.5, 0.5) rotatex(0deg) rotatey(0deg) rotatez(0deg);
  }
  20.25% {
    transform: translateX(-50%) scale3d(0.5, 0.5, 0.5) rotatex(0deg) rotatey(-90deg) rotatez(0deg);
  }
  100% {
    transform: translateX(-50%) scale3d(0.5, 0.5, 0.5) rotatex(0deg) rotatey(-90deg) rotatez(0deg);
  }
}
@keyframes flipY-outerShadow {
  0% {
    transform: translatex(0%) translateY(-300%) rotatex(-90deg) translatez(105px) rotatez(0deg);
  }
  20.25% {
    transform: translatex(0%) translateY(-300%) rotatex(-90deg) translatez(105px) rotatez(90deg);
  }
  100% {
    transform: translatex(0%) translateY(-300%) rotatex(-90deg) translatez(105px) rotatez(90deg);
  }
}
@keyframes flipY-innerShadow {
  0% {
    transform: translatex(0%) translateY(192%) rotatex(-90deg) translatez(-20px) rotatez(0deg);
  }
  20.25% {
    transform: translatex(0%) translateY(192%) rotatex(-90deg) translatez(-20px) rotatez(-90deg);
  }
  100% {
    transform: translatex(0%) translateY(192%) rotatex(-90deg) translatez(-20px) rotatez(-90deg);
  }
}
@keyframes border-front {
  0% {
    border-width: 1px 2px 2px 1px;
  }
  2.25% {
    border-width: 1px 2px 2px 1px;
  }
  5.75% {
    border-width: 1px 1px 1px 1px;
  }
  100% {
    border-width: 1px 1px 1px 1px;
  }
}
@keyframes border-back {
