.cube_container {
  display: flex;
  align-items: center;
  justify-content: center;
}

.scene {
  position: relative;
  width: 20px;
  height: 20px;
  left: 0;
  top: 0;
  transform-style: preserve-3d;
  transform: translateX(0) translateY(0) rotatex(-33.5deg) rotatey(45deg);
  transition: 0.4s all;
}

.scene .webpack-cube {
  transform-style: preserve-3d;
  animation-timing-function: ease-in-out;
  transition: 0.4s all;
}

.scene .outer-cube {
  position: absolute;
  width: 20px;
  height: 20px;
  left: 50%;
  top: -50%;
  transform-style: preserve-3d;
  transform: translateX(-50%) translateY(50%);
  display: inline-block;
  transition: 0.4s all;
}

.scene .outer-cube .face {
  position: absolute;
  width: 100%;
  height: 100%;
  text-align: center;
  background: rgba(255, 255, 255, 0.15);
  line-height: 100px;
  border: 1px solid rgba(255, 255, 255, 0.3);
  transition: 0.4s all;
}

.scene .outer-cube .face-front {
  transform: translatez(10px);
  border-right-width: 2px;
  border-bottom-width: 2px;
}

.scene .outer-cube .face-back {
  transform: rotateY(180deg) translatez(10px);
}

.scene .outer-cube .face-top {
  transform: rotatex(90deg) translatez(10px);
  border-top-width: 2px;
  border-right-width: 2px;
}

.scene .outer-cube .face-bottom {
  transform: rotatex(-90deg) translatez(10px);
}

.scene .outer-cube .face-left {
  transform: rotateY(-90deg) translatez(10px);
  border-left-width: 2px;
  border-bottom-width: 2px;
}

.scene .outer-cube .face-right {
  transform: rotateY(90deg) translatez(10px);
}

.scene .inner-cube {
  display: inline-block;
  position: absolute;
  width: 20px;
  height: 20px;
  left: 45%;
  top: -50%;
  transform-style: preserve-3d;
  transform: translateX(-50%) translateY(50%) scale3d(0.5, 0.5, 0.5);
  transition: 0.4s all;
}

.scene .inner-cube .face {
  position: absolute;
  width: 100%;
  height: 100%;
  text-align: center;
  background: rgba(255, 255, 255, 0.15);
  line-height: 100px;
  border: 1px solid rgba(255, 255, 255, 0.3);
  transition: 0.4s all;
}

.scene .inner-cube .face-front {
  transform: translatez(10px);
}

.scene .inner-cube .face-back {
  transform: rotateY(180deg) translatez(10px);
}

.scene .inner-cube .face-top {
  transform: rotatex(90deg) translatez(10px);
}

.scene .inner-cube .face-bottom {
  transform: rotatex(-90deg) translatez(10px);
}

.scene .inner-cube .face-left {
  transform: rotateY(-90deg) translatez(10px);
}

.scene .inner-cube .face-right {
  transform: rotateY(90deg) translatez(10px);
}

.scene .shadows-outer-container {
  display: inline-block;
  transition: 0.4s all;
}

.scene .shadows-outer-container .shadow-outer {
  display: inline-block;
  transition: 0.4s all;
}

.scene .shadows-inner-container {
  display: inline-block;
  transition: 0.4s all;
}

.scene .shadows-inner-container .shadow-inner {
  display: inline-block;
  transition: 0.4s all;
}

.scene-hover .webpack-cube {
  animation: hoverY 2s infinite alternate;
}

.scene-hover .outer-cube {
  width: 30px;
  height: 30px;
  animation: flipY 4s infinite;
}

.scene-hover .outer-cube .face {
  border: 1px solid rgb(255, 255, 255);
}

.scene-hover .outer-cube .face-front {
  transform: translatez(15px);
  animation: border-front 4s infinite;
}

.scene-hover .outer-cube .face-back {
  transform: rotateY(180deg) translatez(15px);
  animation: border-back 4s infinite;
}

.scene-hover .outer-cube .face-top {
