}

.scene-hover .outer-cube .face-left {
  transform: rotateY(-90deg) translatez(15px);
  animation: border-left 4s infinite;
}

.scene-hover .outer-cube .face-right {
  transform: rotateY(90deg) translatez(15px);
}

.scene-hover .inner-cube {
  width: 30px;
  height: 30px;
  animation: flipY-innerCube 4s infinite;
}

.scene-hover .inner-cube .face {
