.scene-hover .inner-cube .face-right {
  transform: rotateY(90deg) translatez(15px);
}

.scene-hover .shadows-outer-container {
  transform-style: preserve-3d;
  width: 20px;
  height: 20px;
  transform: translatex(0%) rotatex(-90deg) translatez(10px);
  animation: flipY-outerShadow 4s infinite;
}

.scene-hover .shadows-outer-container .shadow-outer {
  position: absolute;
  width: 20px;
  height: 20px;
  background-color: rgba(255, 255, 255, 0.5);
  animation: scaleBigShadow 2s infinite alternate;
  animation-timing-function: ease-in-out;
  box-shadow: 0px 0px 16px 16px rgba(255, 255, 255, 0.5);
