  transform: translatex(-50%) rotatex(-90deg) translatez(0px) rotatez(0deg);
  animation: flipY-innerShadow 4s infinite;
}

.scene-hover .shadows-inner-container .shadow-inner {
  position: absolute;
  width: 20px;
  height: 20px;
  background: linear-gradient(0deg, rgba(255, 255, 255, 0) 10%, rgba(255, 255, 255, 0.15) 50%, rgba(255, 255, 255, 0)), linear-gradient(90deg, rgba(204, 209, 254, 0) 10%, rgba(255, 255, 255, 0.15) 50%, rgba(255, 255, 255, 0)), linear-gradient(180deg, rgba(255, 255, 255, 0) 10%, rgba(255, 255, 255, 0.15) 50%, rgba(255, 255, 255, 0)), linear-gradient(270deg, rgba(255, 255, 255, 0) 10%, rgba(255, 255, 255, 0.15) 50%, rgba(255, 255, 255, 0));
  animation: scaleSmallShadow 2s infinite alternate;
  animation-timing-function: ease-in-out;
  transform-origin: center center;
}

